import React from "react";
import { useTranslation } from "gatsby-plugin-react-i18next";
import { Button } from "react-bootstrap";

import { Edition } from "../../data";
import { members, Member } from "../general/team";
import placeholder from "../../images/OurTeam/placeholder.png";

import * as styles from "./team.module.css";

type TeamMemberProps = {
  member: Member;
};

const TeamMember: React.FC<TeamMemberProps> = ({ member }) => {
  return (
    <Button
      className={styles.teamMember}
      href={member.contact}
      target={"_blank"}
      rel={"noreferrer"}
    >
      <img
        src={member.image ? member.image : placeholder}
        alt={"Photo of " + member.name}
        className={styles.memberImage}
      />
      <p className={styles.memberName}>{member.name}</p>
      <p className={styles.memberDescription}>{member.role}</p>
      <p className={styles.memberDescription}>{member.affiliation}</p>
    </Button>
  );
};

type OurTeamProps = {
  edition: Edition;
};

const OurTeam: React.FC<OurTeamProps> = ({ edition }) => {
  const { t } = useTranslation();
  const currentMembers = members.filter((member) => member.current);
  const pastMembers = members.filter((member) => !member.current);

  return (
    <div className={`page ${styles.team}`} id={"team"}>
      <h1 className={`title ${styles.teamTitle}`}>{t("title")}</h1>
      <div className={styles.memberList}>
        {currentMembers.map((member, index) => (
          <TeamMember key={"teamMember" + index} member={member} />
        ))}
      </div>

      <h2 className={styles.teamSubtitle}>{t("other")}</h2>
      <div className={styles.memberList}>
        {pastMembers.map((member, index) => (
          <TeamMember key={"teamMember" + index} member={member} />
        ))}
      </div>
    </div>
  );
};

export default OurTeam;
